.thesis-slideshow {
  position: relative;
  max-width: 700px;
  margin: 2rem auto;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.1);
}

.thesis-slideshow img {
