}

.slideshow-caption {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.55);
  color: #fff;
  font-size: 1.1rem;
  font-weight: 500;
  text-align: center;
  padding: 0.8rem;
}

/* Font visibility improvements */
.thesis-title {
  margin-top: 10px;
  font-size: 1.6rem;
  font-weight: 600;
  margin-bottom: 1rem;
}

.thesis-meta {
  font-size: 1.1rem;
  color: #374151;
}

.detail-box h3 {
  font-size: 1.3rem;
  color: var(--blue);
  margin-bottom: 0.5rem;
}

.detail-box p {
  font-size: 1.2rem;
  line-height: 1.7;
  color: #1e293b;
}

.thesis-bsc-mt{
  margin-top: 4rem;
}

.thesis-actions .proj-btn{
  margin-right: 1rem;
}

.notice {
  margin-right: 1rem;
  color: #e67e22;              /* orange text */
  font-weight: bold;
  border: 1px solid #e67e22;   /* orange border */
  background-color: #fffaf5;   /* soft orange background */
  padding: 0.55rem 1.2rem;
  font-size: 1rem;
  border-radius: 6px;          /* rounded corners */
  display: inline-block;       /* shrink wrap text */
}


















/* css for the certificate page */
/* Grid */
.cert-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr)); /* larger min width */
  gap: 2.5rem; /* more breathing space */
}

/* Individual card */
.cert-card {
  display: flex;
  flex-direction: column;
  background: #fff;
  border: 1px solid #e2e8f0;
  border-radius: 14px;
  box-shadow: 0 6px 18px rgba(0,0,0,0.06);
  transition: all 0.3s ease;
  overflow: hidden;
  padding: 2rem; /* more padding */
}

.cert-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 12px 24px rgba(0,0,0,0.12);
}

/* Thumbnail */
.cert-thumb {
  flex: 0 0 auto;
  margin-bottom: 1.2rem;
  text-align: center;
}

.cert-thumb img {
  max-height: 160px; /* bigger preview */
  width: auto;
  border-radius: 8px;
  border: 1px solid #e2e8f0;
  object-fit: contain;
  box-shadow: 0 2px 6px rgba(0,0,0,0.05);
}

/* Body */
.cert-body {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.cert-title {
  font-size: 1.4rem; /* bigger */
  font-weight: 600;
  color: #1e293b;
  margin-bottom: 0.6rem;
  line-height: 1.4;
}

.cert-issuer {
  font-size: 1.2rem;
  font-weight: 500;
  color: #475569;
  margin-bottom: 0.4rem;
}

.cert-date {
  font-size: 1.125rem;
  color: #64748b;
  margin-bottom: 1rem;
}

/* Action button */
.cert-action {
  margin-top: auto;
}

.cert-btn {
  background: linear-gradient(135deg, var(--blue), #3b82f6);
  color: white;
  padding: 0.55rem 1.5rem;
  border-radius: 30px;
  font-size: 1rem;
  font-weight: 600;
  text-decoration: none;
  text-align: center;
  transition: all 0.3s ease;
  display: inline-block;
}

.cert-btn:hover {
  color: #f1f4fa;
  transform: translateY(-3px);
  box-shadow: 0 6px 16px rgba(0,0,0,0.15);
}

/* Responsive improvements */
@media (max-width: 768px) {
  .cert-title {
    font-size: 1.1rem;
  }
  .cert-issuer {
    font-size: 0.95rem;
  }
  .cert-date {
    font-size: 0.85rem;
  }
}
























/* -------CSS for the Experience Section ------- */
.experience-list {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

/* ------- Single Entry ------- */
.experience-entry {
  display: flex;
  align-items: flex-start;
  border-bottom: 1px solid var(--border);
  padding-bottom: 1.5rem;
  gap: 1.5rem;
}

.exp-meta img {
  max-width: 50px;
  max-height: 50px;
  object-fit: contain;
  transition: transform 0.3s ease;
}

.experience-entry:hover .exp-meta img {
  transform: scale(1.05);
}

.exp-details {
  flex: 1;
}

.exp-header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.exp-details h4 {
  font-size: 1.45rem;
  font-weight: 600;
  margin: 0;
  color: var(--dark);
}

.exp-details h5 {
  font-size: 1.25rem;
  font-weight: 500;
  margin: 0.3rem 0;
  color: var(--text);
}

.exp-period {
  display: inline-block;
  font-size: 1rem;
  font-weight: 600;
  color: var(--blue);              
  background: #e8f0fd;            
  padding: 0.25rem 0.8rem;
  border-radius: 12px;
  margin-top: 0.4rem;
  margin-bottom: 0.4rem;
}


/* Responsibilities list */
.exp-responsibilities {
  margin-top: 0.5rem;
  list-style: none;
  padding-left: 1rem;
}

.exp-responsibilities li {
  font-size:1.25rem;
  color: var(--text);
  margin-bottom: 0.4rem;
  position: relative;
}

.exp-responsibilities li::before {
  content: "•";
  position: absolute;
  left: -0.8rem;
  color: var(--blue);
}

/* Responsive */
@media (max-width: 768px) {
  .experience-entry {
    flex-direction: column;
    align-items: flex-start;
  }

  .exp-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.3rem;
  }

  .exp-period {
    margin-left: 0;
  }
}











/* css for blog page */
.blog-card{
    box-shadow: 3px 3px 5px hsla(0, 0%, 0%, 0.5);
}

.blog-des{
    margin-left: 46%;
}

.blog-img{
    width: 45%;
    float: left;
    text-align: center;
}

.blog-img img {
    width: 70%;
}

/* Additional styles for React components */
.main-content {
    min-height: calc(100vh - 80px);
}

/* Thesis specific styles */
.heading {
    font-size: 4rem;
    color: var(--blue);
    text-align: center;

}

.heading span {
    color: var(--blue);
}

.project-holder {
    padding: 2rem;
}

.project-des {
    font-size: 16px;
    line-height: 1.6;
    margin-bottom: 2rem;
    text-align: justify;
}

.project-img-holder {
    margin: 3rem 0;
}

.img-title {
    text-align: center;
    font-weight: bold;
    margin-top: 1rem;
    font-size: 14px;
}



.butn:hover {
    box-shadow: 0px 10px 15px rgba(82, 159, 178, 0.6);
    transform: translateY(-2px);
}

.publication-btn-1 {
    margin-right: 1rem;
}





/* -------- Global Consistent Page Layout -------- */
.container,
.home .container,
.publication .container,
.skill .container,
.projects-section .container,
.certification .container,
.experience .container,
.blog .container,
.thesis .container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 1.5rem;
  padding-right: 1.5rem;

  /* Unified top margin */
  margin-top: 7rem;
}




/* Global background (light mode default) */
body {
  background: #f9fafb; /* soft white/gray like pro sites */
  color: rgba(0,0,0,0.85);
  transition: background 0.3s ease, color 0.3s ease;
}

/* Dark mode styles */
body.dark-mode {
  background: #111827; /* near-black */
  color: #e5e7eb;
}

body.dark-mode h1,
body.dark-mode h2,
body.dark-mode h3,
body.dark-mode .any-heading {
  color: #93c5fd; /* soft blue for headings */
}

body.dark-mode .card,
body.dark-mode .skill-box,
body.dark-mode .cert-card,
body.dark-mode .project-entry,
body.dark-mode .experience-entry {
  background: #1f2937;
  border-color: #374151;
  color: #e5e7eb;
}


/* Dark mode base */
body.dark-mode {
  background: #272935; /* dark background */
  color: #e5e7eb;      /* default text color */
}

body.dark-mode .name{
  color: #e5e7eb;
}

/* Keep headings unchanged (blue) */
body.dark-mode h1,
body.dark-mode h2,
body.dark-mode h3,
body.dark-mode h4,
body.dark-mode h5,
body.dark-mode h6,
body.dark-mode .any-heading-marginless,
body.dark-mode .section-subheading {
   color: #e5e7eb !important;
}

body.dark-mode a i{
  color: var(--blue);
}

body.dark-mode .dark_i{
  color: #e5e7eb !important;
}

body.dark-mode a button i{
  color: #e5e7eb !important;
}



/* Text, paragraphs, and descriptions → white */
body.dark-mode p,
body.dark-mode li,
body.dark-mode i,
body.dark-mode .introduction,
body.dark-mode .project-desc,
body.dark-mode .pub-info p,
body.dark-mode .cert-issuer,
body.dark-mode .cert-date,
body.dark-mode .exp-details h5,
body.dark-mode .exp-period {
 color:  #98a6ad; 
}

/* Cards and containers adapt */
body.dark-mode .card,
body.dark-mode .skill-box,
body.dark-mode .cert-card,
body.dark-mode .project-entry,
body.dark-mode .experience-entry {
  background: #1f2937;
  border-color: #374151;
  color: #e5e7eb;
}
